@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
/* custom colors  */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

:root {
  --bgcolor: #f1f5fd;
  --primary-color: #6f5de7;
  --text-color: #828286;
  --main-text-color: #333333;
}

/* html {
  font-size: 62.5%;
} */

a {
  text-decoration: none;
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333333;
}


/* ----------- navbar css style starts ---------------*/

.navbar-bg {
  background-color: #f1f5fd;
  background-color: var(--bgcolor);
  padding: 1rem 0;
  z-index: 99;
  position: relative;
}

.navbar-brand {
  font-size: 2.5rem;
  color: blue !important;
  font-weight: bold;
}

.navbar li {
  padding: 0 0.5rem;
}

.navbar li a {
  font-size: 1rem;
  font-weight: lighter;
  position: relative;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

.navbar li a:hover {
  color: #6f5de7 !important;
  color: var(--primary-color) !important;
  text-shadow: 0 0.2rem 0.3rem rgba(183, 171, 255, 0.5);
}

.btn-style {
  width: 7rem;
  height: 3rem;
  border-radius: 2rem;
  color: #fff;
  background-color: #6f5de7;
  background-color: var(--primary-color);
  text-transform: capitalize;
  font-size: 1rem;
}

.btn-style:first-child {
  margin-right: 1rem;
}

.btn-style:hover {
  border: 0.1rem solid #6f5de7;
  border: 0.1rem solid var(--primary-color);
  background-color: transparent;
  color: #6f5de7;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}

.btn-style-border {
  border: 0.1rem solid #6f5de7;
  border: 0.1rem solid var(--primary-color);
  background-color: transparent;
  color: #6f5de7;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}

.btn-style-border:hover {
  color: #fff;
  background-color: #6f5de7;
  background-color: var(--primary-color);
  text-transform: capitalize;
}                       
                        
/* -------- Navbar ends  --------------------*/
  
 /* #Main Landing Section  */

 #header {
   width: 100%;
   height: 80vh;
   margin-top: 70px;
 }

 #header h1 {
   margin: 0 0 10px 0;
   font-size: 40px;
   font-weight: 700;
   color: black;
 }

 #header .brand-name {
   color: #3498db;
 }

 #header h2 {
   color: #484848;
   font-size: 24px;
 }

 #header .btn-get-started {
   font-weight: 500;
   font-size: 16px;
   letter-spacing: 1px;
   display: inline-block;
   padding: 10px 30px;
   border-radius: 50px;
   transition: 0.5s;
   color: #3498db;
   border: 2px solid #3498db;
   text-decoration: none;
 }

 #header .btn-get-started:hover {
   background: #3498db;
   color: #fff;
 }

 .header-img {
   text-align: right;
   
 }

 #header .animated {
   animation: up-down 2s ease-in-out infinite alternate-reverse both;
 }


body{
	line-height: 1.0;
	font-family: 'Poppins', sans-serif;
   
}
*{
	margin:0;
	padding:0;
	box-sizing: border-box;
}
.container{
	max-width: 1170px;
	margin:auto;
   }
.row{
	display: flex;
	flex-wrap: wrap;
}
ul{
	list-style: none;
}
.footer{
    position: relative;
    bottom: 0;
    width: 100%;
	background-color: #24262b;
    padding: 20px 0;
    
}
.footer-col{
   width: 25%;
   padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left:0;
	bottom: -10px;
	background-color: #e91e63;
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: #ffffff;
	padding-left: 8px;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}

